import SlimSelect from 'slim-select'


const moduleName = 'customSelect';

export function customSelect() {

    Array.from(document.querySelectorAll('.js-select')).forEach((self) => {
        const placeholder = self.dataset.placeholder;
        const wrap = self.closest('.select-wrap');
        new SlimSelect({
            select: self,
            showSearch: false,
            placeholder: placeholder,
            beforeOpen: () => {
                if(wrap) wrap.classList.add('_opened');
            },
            beforeClose: function () {
                if(wrap) wrap.classList.remove('_opened');
            }
        })
    });

    Array.from(document.querySelectorAll('.js-search-select')).forEach((self) => {
        const placeholder = self.dataset.placeholder;
        $(self).selectize({
            placeholder: placeholder,
            hideSelected: false,
            delimiter: ',',
            onChange: self.onchange
        });
    });

    Array.from(document.querySelectorAll('.js-select-deselect')).forEach((self) => {
        const placeholder = self.dataset.placeholder;
        const wrap = self.closest('.select-wrap');
        new SlimSelect({
            select: self,
            showSearch: false,
            placeholder: placeholder,
            allowDeselect: true,
            beforeOpen: () => {
                if(wrap) wrap.classList.add('_opened');
            },
            beforeClose: function () {
                if(wrap) wrap.classList.remove('_opened');
            }
        })
    });

    Array.from(document.querySelectorAll('.js-price-select')).forEach((self) => {
        let data;
        try{
            data = priceData;
        }catch (e){
            if (e instanceof ReferenceError) {
                console.error('No data for price select!');
                return false;
            }
        }
        new SlimSelect({
            select: self,
            showSearch: false,
            data: data
        })
    });

    Array.from(document.querySelectorAll('.js-payments-select')).forEach((self) => {
        let data;
        try{
            data = paymentsData;
        }catch (e){
            if (e instanceof ReferenceError) {
                console.error('No data for payment select!');
                return false;
            }
        }
        new SlimSelect({
            select: self,
            showSearch: false,
            data: data
        })
    });

    window.SlimSelect = SlimSelect;

}
