// jQuery
import jQuery from 'jquery';
window.$ = window.jQuery = jQuery;

// modules
import { svgLoad } from './modules/svg';
import { lazyLoad } from "./modules/lazyload";
import { popups } from './modules/popups';
import { menu } from './modules/menu';
import {customSelect} from "./modules/customSelect";
import {cmplx} from "./modules/cmplxInput";
import {hidden} from "./modules/hidden";
import {chat} from "./modules/chat";
import {forms} from "./modules/forms";
import {carousel} from "./modules/carousel";
import {tooltip} from "./modules/tooltip";
import {rating} from "./modules/rating";
import {scrollTo} from "./modules/scrollTo";
import {tabs} from "./modules/tabs";
import {user} from "./modules/user";
import {schedule} from "./modules/schedule";
import {dependent} from "./modules/dependent";
import {customDatepicker} from "./modules/datepicker";
import {calendar} from "./modules/calendar";
import {video} from "./modules/video";
import {filter} from "./modules/filter";

require('air-datepicker/dist/js/datepicker');
// require('semantic-ui-dropdown/dropdown');
// require('semantic-ui-transition/transition');
require('selectize/dist/js/selectize')

window.initModules = () => {
    svgLoad();
    lazyLoad();

    customSelect();
    filter();
    carousel();
    rating();
    video();
    popups();
    menu();
    cmplx();
    hidden();
    chat();
    forms();
    tooltip();
    scrollTo();
    tabs();
    user();
    schedule();
    dependent();
    customDatepicker();
    calendar();

};

if (document.readyState === 'complete') {
    initModules();
} else if (document.readyState === 'interactive') {
    initModules();
} else {
    document.addEventListener('DOMContentLoaded', () => {
        initModules();
    });
}
