const moduleName = 'forms';

export function forms() {
    document.querySelectorAll('.js-files').forEach((file) => {
        const input = file.querySelector('.js-files-input');
        const countLabel = file.querySelector('.js-files-count');

        input.addEventListener('change', function (e) {
            let countFiles = '';
            if (this.files && this.files.length >= 1)
                countFiles = this.files.length;

            if (countFiles){
                file.classList.add('_filled');
                countLabel.innerText = `${countFiles}`;
            }else{
                file.classList.remove('_filled');
                countLabel.innerText = '';
            }

        });
    });

    document.querySelectorAll('form.js-no-enter-submit').forEach((form) => {
        form.addEventListener('keydown', function (e){
            if(e.keyCode == 13) {
                e.preventDefault();
                return false;
            }
        });
    });

    document.querySelectorAll('.js-has-related').forEach((input) => {
        input.addEventListener('change', function (){
            const related = input.parentNode.querySelector('.js-related');
            if(input.checked){
                related.classList.add('_visible');
            }else {
                related.classList.remove('_visible');
            }
        });
    });
}
