export function svgLoad() {
    let svgPath = document.querySelectorAll('body')[0].dataset.svgPath;
    if (typeof svgPath == 'undefined') { svgPath = ''; }
    const ajax = new XMLHttpRequest();
    ajax.open('GET', svgPath + 'images/sprite.svg', true);
    ajax.send();
    ajax.onload = function (e) {
        const block = document.createElement('div');
        block.innerHTML = ajax.responseText;
        block.classList.add('svg-sprite-wrap');
        document.body.insertBefore(block, document.body.childNodes[0]);
    };

    const ajax2 = new XMLHttpRequest();
    ajax2.open('GET', svgPath + 'images/sprite-color.svg', true);
    ajax2.send();
    ajax2.onload = function (e) {
        let block = document.querySelector('.svg-sprite-wrap');
        if (block) {
            block.innerHTML += ajax2.responseText;
        } else {
            block = document.createElement('div');
            block.innerHTML = ajax2.responseText;
            block.classList.add('svg-sprite-wrap');
            document.body.insertBefore(block, document.body.childNodes[0]);
        }
    };
}

export function svgFix(){
    document.addEventListener("DOMContentLoaded", function () {
        let baseUrl = window.location.href
            .replace(window.location.hash, "");

        [].slice.call(document.querySelectorAll("use[*|href]"))

            .filter(function (element) {
                return (element.getAttribute("xlink:href").indexOf("#") === 0);
            })

            .forEach(function (element) {
                element.setAttribute("xlink:href", baseUrl + element.getAttribute("xlink:href"));
            });

    }, false);
}

