const moduleName = 'chat';

export function chat() {

    function initChatUsersList() {
        document.querySelectorAll('.js-user-chat-link').forEach((btn) => {
            btn.addEventListener('click', function (){
                const users = btn.parentNode;
                if(!btn.classList.contains('_current')) {
                    const currentBtn = btn.closest('.chat').querySelector('.js-user-chat-link._current');
                    const openedChat = btn.closest('.chat').querySelector('.js-messages-stack._opened');
                    const chat = document.querySelector(`#${btn.dataset.chatId}.js-messages-stack`);
                    if(currentBtn) currentBtn.classList.remove('_current');
                    btn.classList.add('_current');
                    openedChat.classList.remove('_opened');
                    chat.classList.add('_opened');
                    if(users.classList.contains('_opened')){
                        users.classList.remove('_opened');
                    }
                }else {
                    if(window.matchMedia('(max-width: 540px)').matches) {
                        const users = btn.parentNode;
                        if(users.classList.contains('_opened')){
                            users.classList.remove('_opened');
                        }else{
                            users.classList.add('_opened');
                        }
                    }
                }
            });
        });
    }

    initChatUsersList();

    window.initChatUsersList = initChatUsersList;
}
