import { hasScrollbar, scrollBarWidth } from './scrollBarWidth';

const moduleName = 'menu';

export function menu () {
    const hasScroll = hasScrollbar();
    const menu = document.querySelector('.js-main-menu');
    const openMenuBtn = document.querySelector('.js-open-main-menu');
    const pageHeader = document.querySelector('.js-page-header');

    const closeMainMenu = function (menu, btn, header) {
        menu.classList.add('_hidden');
        btn.classList.remove('_active');
        const overlay = header.querySelector('.overlay');
        overlay.classList.add('_hidden');
        //overlay.classList.remove('_visible');
        setTimeout(function () {
            menu.classList.remove('_visible');
            menu.classList.remove('_hidden');
            overlay.parentNode.removeChild(overlay);
        }, 300);
        document.body.classList.remove('has-overlay');
        // if (hasScroll) {
        //     document.body.style.paddingRight = 0;
        // }
        header.classList.remove('_active');
    }

    const openMainMenu = function (menu, btn, header) {
        // const info = document.querySelector('.js-info');
        //let pad = header.clientHeight - window.scrollY + 28;
        // let pad = header.clientHeight - window.scrollY;
        // if (info) {
        //     pad += info.clientHeight;
        // }
        //menu.style.paddingTop = `${pad}px`;
        //menu.style.top = `${pad}px`;
        menu.classList.add('_visible');
        const overlay = document.createElement('div');

        overlay.classList.add('overlay');
        document.body.classList.add('has-overlay');
        header.appendChild(overlay);
        //document.querySelector('.page').appendChild(overlay);
        overlay.classList.add('_visible');

        btn.classList.add('_active');

        overlay.addEventListener('click', (e) => {
            const target = e.target;

            if (menu !== target && !menu.contains(target)) {
                closeMainMenu(menu, btn, header);
            }
        }, false);

        // if (hasScroll) {
        //     document.body.style.paddingRight = `${scrollBarWidth()}px`;
        // }
        header.classList.add('_active');
    }

    if (menu && openMenuBtn) {
        openMenuBtn.addEventListener('click', function () {
            if (menu.classList.contains('_visible')) {
                closeMainMenu(menu, openMenuBtn, pageHeader);
            } else {
                openMainMenu(menu, openMenuBtn, pageHeader);
            }
        });
        // closeMenuBtn.addEventListener('click', function () {
        //     closeMainMenu(menu, openMenuBtn);
        // });
    }

}
