const moduleName = 'rating';

export function rating () {
    const ratingInit = () => document.querySelectorAll('.js-mark').forEach(el => {
        const mark = el.dataset.mark;
        if (mark) {
            const items = [];
            for (let i = 1; i < 6; i++) {
                const item = document.createElement('span');
                item.className = 'mark__item';
                if (i <= mark) {
                    item.classList.add('mark__item--filled');
                }
                items.push(item);
            }
            el.append(...items);
        }
    });
    ratingInit();
    window.ratingInit = ratingInit;

    const ratingBtns = document.querySelectorAll('.js-rating-btn');
    const ratingInputs = document.querySelectorAll('.js-rating-input');
    ratingBtns.forEach(btn => {
        const allRatingBtnsArr = Array.from(btn.parentNode.querySelectorAll('.js-rating-btn'));
        const i = allRatingBtnsArr.indexOf(btn);
        btn.addEventListener('mouseover', function () {
            allRatingBtnsArr.filter((item, index) => {
                return index <= i;
            }).forEach(item => {
                item.classList.add('is-filled');
            });
        });

        btn.addEventListener('mouseout', function () {
            allRatingBtnsArr.forEach(item => {
                item.classList.remove('is-filled');
            });
        });

        btn.addEventListener('click', function () {
            const input = this.parentNode.querySelector('.js-rating-input');
            const textWrap = this.parentNode.nextElementSibling;
            const text = textWrap.querySelector('.js-rating-text');

            allRatingBtnsArr.forEach(item => {
                item.classList.remove('is-selected');
            });

            allRatingBtnsArr.filter((item, index) => {
                return index <= i;
            }).forEach(item => {
                item.classList.add('is-selected');
            });

            input.value = i + 1;

            textWrap.classList.add('_visible');
            text.textContent = allRatingBtnsArr.filter((item, index) => {
                return index === input.value - 1;
            })[0].dataset.mark;
        });
    });

    ratingInputs.forEach(input => {
        const allRatingBtnsArr = Array.from(input.parentNode.querySelectorAll('.js-rating-btn'));
        allRatingBtnsArr.filter((item, index) => {
            return index < input.value * 1;
        }).forEach(item => {
            item.classList.add('is-selected');
        });
    });

}
