const moduleName = 'schedule';

export function schedule() {
    document.querySelectorAll('.js-booking-btn').forEach((btn) => {
        btn.addEventListener('click', function (){
            const schd = btn.closest('.js-schedule');
            const tools = schd.parentNode.querySelector('.js-schedule-tools');
            if(btn.classList.contains('_active') && !btn.classList.contains('_pending')){
                schd.classList.add('_pending');
                btn.classList.add('_pending');
                tools.classList.add('_visible');
                return;
            }
            if(schd.classList.contains('_pending')){
                const pending = schd.querySelector('._pending');
                pending.classList.remove('_active');
                pending.classList.remove('_pending');
                schd.classList.remove('_pending');
                tools.classList.remove('_visible');
                if(!btn.classList.contains('_pending')) btn.classList.add('_active');
            }

            if(btn.classList.contains('_free')){
                btn.classList.remove('_free');
                btn.classList.add('_ready');
                return;
            }
            if(btn.classList.contains('_ready')){
                btn.classList.remove('_ready');
                btn.classList.add('_free');
                return;
            }

            //btn.classList.add('_active','block-tooltip');
        });
    });

}
