    document.addEventListener('click', function (e) {
        const target = e.target;

        if (target.tagName === 'LABEL' && target.parentNode.querySelector('input.js-time')) {
            const timeContainer = target.closest('.js-time-container');
            const changeFormControl = target.closest('form').querySelector('.change-form-controls'); // "разово/регулярно"
            const saveFormControl = target.closest('form').querySelector('.save-form-controls'); // "Сохранить"

            if (target.parentNode.querySelector('input.js-time').classList.contains('_active')) {
                e.preventDefault();
                const input = target.parentNode.querySelector('input');
                if (input.classList.contains('_pending')) {
                    input.classList.remove('_pending');
                    timeContainer.classList.remove('_pending');
                    changeFormControl.classList.remove('_active'); // видимость переключателей "разово/регулярно"
                    // saveFormControl.classList.remove('_active');
                } else {
                    input.classList.add('_pending');
                    timeContainer.classList.add('_pending');
                    changeFormControl.classList.add('_active'); // видимость переключателей "разово/регулярно"
                    saveFormControl.classList.add('_active');
                }
            } else {
                const pendingInput = timeContainer.querySelector('._pending');
                const input = target.parentNode.querySelector('input');

                if (pendingInput) {
                    input.classList.add('_active');
                    pendingInput.classList.remove('_active', '_pending');
                    pendingInput.checked = false;
                    timeContainer.classList.remove('_pending');
                    // changeFormControl.classList.remove('_active');
                } else {

                }

                saveFormControl.classList.add('_active');
            }

            checkActiveDays(target.closest('.time-setting'));
        }
    });

    function checkActiveDays (timeSettingWrapper) {
        const days = timeSettingWrapper.querySelectorAll('.js-show-time');
        days.forEach(day => {
            const timeId = day.dataset.dependent;
            const timeGroup = timeSettingWrapper.querySelector(`#${timeId}`);
            const times = timeGroup.querySelectorAll('.js-time');
            const isActive = Array.from(times).filter(time => time.classList.contains('_active')).length > 0;

            isActive ? day.classList.add('_active') : day.classList.remove('_active');
        });
    }

