import {isVisible} from "./utils";

const moduleName = 'user';

export function user () {

    const closeLightOverlay = () => {
        const overlay = document.body.querySelector('.light-overlay');
        overlay.classList.add('_hidden');
        setTimeout(function () {
            overlay.parentNode.removeChild(overlay);
        }, 300);
    }

    const closePanel = (panel) => {
        panel.classList.add('_hidden');
        setTimeout(function () {
            panel.classList.remove('_visible');
            panel.classList.remove('_hidden');
        }, 300);
    }

    document.querySelectorAll('.js-open-panel').forEach((btn) => {
        btn.addEventListener('click', function (){
            const panel = this.parentNode.querySelector('.js-panel');
            if(isVisible(panel)){
                closePanel(panel);
            }else{
                panel.classList.add('_visible');
            }
            btn.classList.toggle('_active');
            document.body.classList.toggle('has-light-overlay');
            if(document.body.classList.contains('has-light-overlay')){
                const overlay = document.createElement('div');
                overlay.classList.add('light-overlay');
                document.body.appendChild(overlay);
                overlay.classList.add('_visible');
                overlay.addEventListener('click', (e) => {
                    const target = e.target;
                    if (panel !== target && !panel.contains(target)) {
                        closeLightOverlay();
                        closePanel(panel);
                        btn.classList.toggle('_active');
                        document.body.classList.toggle('has-light-overlay');
                    }
                }, false);

            }else{
                closeLightOverlay();
            }
        });
    });

    document.querySelectorAll('.user-controls .js-open-modal').forEach((btn) => {
        btn.addEventListener('click', function (){
            btn.classList.add('_active');
        });
    });
}
