const moduleName = 'calendar';

export function calendar() {

    document.addEventListener('click', function (e){
        const target = e.target;
        if(target.classList.contains('js-open-calendar')){
            const calendar = target.parentNode;
            calendar.classList.toggle('_opened');
        }
    });

    document.querySelectorAll('.js-calendar').forEach((calendar) => {
        const btn = calendar.querySelector('.js-open-calendar');
        const input = calendar.querySelector('.js-calendar-input');
        input.addEventListener('change', function (){
            btn.textContent = input.value;
        });
        btn.textContent = input.value;
    });


    $.fn.datepicker.language['en'] = {
        days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
        daysShort: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
        daysMin: ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'],
        months: ['January','February','March','April','May','June', 'July','August','September','October','November','December'],
        monthsShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
        today: 'Today',
        clear: 'Clear',
        dateFormat: 'M D, yyyy',
        timeFormat: 'hh:ii aa',
        firstDay: 0
    };

    document.querySelectorAll('.js-date-range').forEach(function (item) {
        const datepicker = $(item).datepicker({
            inline: true,
            range: true,

            calendar: 2,
            //adaptiveOrder: true,

            language: 'en',
            dateFormat: 'dd/mm/yyyy',
            multipleDatesSeparator: '-',
            classes: 'big-datepicker',
            // navTitles: {
            //     days: 'MM',
            //     months: 'MM',
            //     years: 'yyyy'
            // },

            prevHtml: '<svg class="svg-icon" width="11" height="20"><use xlink:href="#left-2"></use></svg>',
            nextHtml: '<svg class="svg-icon" width="11" height="20"><use xlink:href="#right-2"></use></svg>',
            onSelect: function (formattedDate, date, inst){
                const event = document.createEvent("HTMLEvents");
                event.initEvent("change", true, true);
                event.eventName = "change";
                item.dispatchEvent(event);
            }

        }).data('datepicker');

        const startDate = item.dataset.start;
        const endDate = item.dataset.end;
        datepicker.selectDate([new Date(startDate), new Date(endDate)]);

        const event = document.createEvent("HTMLEvents");
        event.initEvent("change", true, true);
        event.eventName = "change";
        item.dispatchEvent(event);

    });

}
