import { slideToggle } from './utils';

const moduleName = 'hidden';

export function hidden() {
    const wrapHidden = () => {
        const hiddens = document.querySelectorAll('.js-hidden-wrap');
        if(hiddens){
            hiddens.forEach((wrap) => {
                if(!wrap.querySelector('.hidden-content')) {
                    const mobLimit = wrap.dataset.mobLimit;
                    const desktopLimit = wrap.dataset.limit;
                    const limit = window.matchMedia('(max-width:480px)').matches ? mobLimit : desktopLimit;
                    const ps = Array.from(wrap.querySelectorAll('p')).filter((item,index,arr) => {
                        return index > limit - 1;
                    });

                    if(ps.length){
                        wrap.insertAdjacentHTML('beforeEnd', '<div class="hidden-content"></div>');
                        const wrapper = wrap.querySelector('.hidden-content');
                        ps.map(function(e){
                            wrapper.appendChild(e)
                        });
                        wrapper.previousElementSibling.insertAdjacentHTML('beforeEnd','<a href="javascript:;" class="toggle-text-btn js-toggle-hidden-text">Show more>></a>');
                    }
                }
            });
        }
    }

    wrapHidden();

    window.wrapHidden = wrapHidden;

    document.addEventListener('click', function (e){
        const target = e.target;
        if(target.classList.contains('js-toggle-hidden')){
            const content = document.querySelector(`#${target.dataset.hiddenId}`);
            let display = 'block';
            target.classList.toggle('_active');
            if(target.classList.contains('_active')){
                target.textContent = target.dataset.activeCaption;
            }else{
                target.textContent = target.dataset.defaultCaption;
            }
            if(content.classList.contains('flex')) display = 'flex';
            slideToggle(content, 250,display);
        }
        if(target.classList.contains('js-toggle-hidden-text')){
            const content = target.parentNode.parentNode.querySelector('.hidden-content');
            slideToggle(content, 250,'block');
            target.style.display = "none";
        }
    });
}
