const moduleName = 'dependent';
export function dependent () {
    document.querySelectorAll('.js-has-dep').forEach(function (item) {
        item.addEventListener('change', function (e) {
            const target = e.target;
            const dep = document.querySelector(`#${target.dataset.dependent}`);
            const wrap = target.closest('.js-wrap-dep');
            if (target.classList.contains('js-show-dep')) {
                dep.classList.add('_opened');
                if (wrap) {
                    wrap.classList.add('_opened');
                }
            } else {
                dep.classList.remove('_opened');
                if (wrap) {
                    wrap.classList.remove('_opened');
                }
            }
        });
    });
}
