import Flickity from 'flickity';

const moduleName = 'carousel';

export function carousel() {

    const max480 = window.matchMedia('(max-width: 480px)');
    const max480min361 = window.matchMedia('(min-width: 361px) and (max-width: 480px)');
    const max360 = window.matchMedia('(max-width: 360px)');
    const max768 = window.matchMedia('(max-width: 768px)');

    Flickity.prototype._createResizeClass = function() {
        this.element.classList.add('flickity-resize');
    };

    Flickity.createMethods.push('_createResizeClass');

    const resize = Flickity.prototype.resize;
    Flickity.prototype.resize = function() {
        this.element.classList.remove('flickity-resize');
        resize.call( this );
        this.element.classList.add('flickity-resize');
    };

    window.Flickity = Flickity;

    function refreshSlider(slider) {
        const flkty = Flickity.data(slider);
        if (flkty) flkty.resize();
    }

    const scheduleCarouselInit = () => document.querySelectorAll('.js-schedule-carousel').forEach((slider) => {
        const schedule = slider.closest('.js-schedule');
        const prevBtns = schedule.querySelectorAll('.js-schedule-prev');
        const nextBtns = schedule.querySelectorAll('.js-schedule-next');
        const title = schedule.querySelector('.js-schedule-title');
        const expand = schedule.querySelector('.js-expand-schedule');
        const scheduleOptions = {
            contain: true,
            wrapAround: false,
            prevNextButtons: false,
            pageDots: false,
            percentPosition: true,
            cellAlign: 'left',
            resize: true,
            draggable: false,
            on: {
                change: function (index) {
                    const selected = flkty.selectedElement;
                    const selectedIndex = flkty.selectedIndex;
                    if(selectedIndex % 7 === 0){
                        const end = flkty.cells[index + 6].element;
                        title.textContent = `${selected.dataset.month} ${selected.dataset.day} - ${end.dataset.month} ${end.dataset.day}`;
                        return;
                    }
                    if(!max360.matches) {
                        if(selectedIndex === 3 || selectedIndex % 7 === 3){
                            const start = flkty.cells[index - 3].element;
                            const end = flkty.cells[index + 3].element;
                            title.textContent = `${start.dataset.month} ${start.dataset.day} - ${end.dataset.month} ${end.dataset.day}`;
                        }
                    }else{
                        if(selectedIndex % 7 === 5){
                            const start = flkty.cells[index - 5].element;
                            const end = flkty.cells[index + 1].element;
                            title.textContent = `${start.dataset.month} ${start.dataset.day} - ${end.dataset.month} ${end.dataset.day}`;
                        }
                    }

                }
            }
        };

        if(max480.matches){
            scheduleOptions.draggable = true;
        }

        const flkty = new Flickity(slider, scheduleOptions);

        prevBtns.forEach((btn) => {
            btn.addEventListener('click', function () {
                const selectedIndex = flkty.selectedIndex;
                if(max480min361.matches){
                    if(selectedIndex % 7 === 0){
                        flkty.select(selectedIndex - 4);
                    }else{
                        flkty.select(selectedIndex - 1);
                    }
                    return;
                }
                if(max360.matches){
                    if(selectedIndex % 7 === 0){
                        flkty.select(selectedIndex - 4);
                        //flkty.select(selectedIndex - 2);
                    }else{
                        flkty.select(selectedIndex - 1);
                    }
                    return;
                }

                if (selectedIndex - 7 < 0) {
                    flkty.select(0);
                } else {
                    flkty.select(selectedIndex - 7);
                }

            });
        });

        nextBtns.forEach((btn) => {
            btn.addEventListener('click', function () {
                const selectedIndex = flkty.selectedIndex;
                if(max480min361.matches){
                    if(selectedIndex % 3 === 0 && selectedIndex % 7 !== 0){
                        flkty.select(selectedIndex + 4);
                    }else{
                        flkty.select(selectedIndex + 1);
                    }
                    return;
                }
                if(max360.matches){
                    if(selectedIndex % 3 === 0 && selectedIndex % 7 !== 0){
                    //if(selectedIndex % 7 === 5 && selectedIndex % 7 !== 0){
                        flkty.select(selectedIndex + 4);
                        //flkty.select(selectedIndex + 2);
                    }else{
                        flkty.select(selectedIndex + 1);
                    }
                    return;
                }

                if(selectedIndex + 7 > 7) {
                    flkty.select(7);
                } else {
                    flkty.select(selectedIndex + 7);
                }

            });
        });

        expand.addEventListener('change', function (e){
            if(this.checked){
                schedule.classList.add('_expanded');
            }else{
                schedule.classList.remove('_expanded');
            }
            flkty.resize();
        });
    });
    scheduleCarouselInit();
    window.scheduleCarouselInit = scheduleCarouselInit;

    const anyJsCarouselInit = () => document.querySelectorAll('.js-carousel').forEach((slider) => {
        const wrap = slider.closest('.slider-wrap');
        const prevBtn = wrap.querySelector('.js-prev');
        const nextBtn = wrap.querySelector('.js-next');
        const carouselOptions = {
            contain: true,
            wrapAround: false,
            prevNextButtons: false,
            pageDots: false,
            percentPosition: true,
            cellAlign: 'left',
            resize: true,
            draggable: true

        };

        if(max768.matches){
            carouselOptions.freeScroll = true;
        }

        const flkty = new Flickity(slider, carouselOptions);
        prevBtn.addEventListener('click', function (){
            flkty.previous(false,false);
        });
        nextBtn.addEventListener('click', function (){
            flkty.next(false,false);
        });
    });
    anyJsCarouselInit();
    window.anyJsCarouselInit = anyJsCarouselInit;


    document.querySelectorAll('.js-mob-scrolling').forEach((slider) => {
        const mobScrollingOptions = {
            contain: true,
            wrapAround: false,
            prevNextButtons: false,
            pageDots: false,
            percentPosition: true,
            cellAlign: 'left',
            resize: true,
            draggable: true,
            freeScroll: true,
            watchCSS: true,
            on: {
                ready: function() {
                    const height = this.element.offsetHeight;
                    this.cells.forEach(slide => {
                        slide.element.style.height = `${height}px`;
                    });
                }
            }
        };

        const flkty = new Flickity(slider, mobScrollingOptions);

    });

}
