const moduleName = 'filter';

export function filter () {
    const header = document.querySelector('.js-page-header');
    const filters = document.querySelector('.js-filter');

    const closeFilters = function (filters, btn,header) {
        filters.classList.remove('_visible');
        btn.classList.remove('_active');
        const overlay = header.querySelector('.overlay');
        overlay.classList.add('_hidden');
        setTimeout(function () {
            overlay.parentNode.removeChild(overlay);
        }, 300);
        document.body.classList.remove('has-overlay');
        header.classList.remove('_active');
    }

    const openFilters = function (filters, btn,header) {
        filters.classList.add('_visible');
        const overlay = document.createElement('div');
        overlay.classList.add('overlay','overlay--sidebar');
        document.body.classList.add('has-overlay');
        header.appendChild(overlay);
        overlay.classList.add('_visible');
        btn.classList.add('_active');
        overlay.addEventListener('click', (e) => {
            const target = e.target;
            if (filters !== target && !filters.contains(target)) {
                closeFilters(filters, btn, header);
            }
        }, false);
        header.classList.add('_active');
    }

    if(filters){
        document.querySelector('.js-open-filter').addEventListener('click', function (){
            openFilters(filters,this,header)
        });

        document.querySelector('.js-close-filter').addEventListener('click', function (){
            closeFilters(filters,this,header)
        });


        //const max540 = window.matchMedia('(max-width: 540px)');
        const min541 = window.matchMedia('(min-width: 541px)');
        //const mobFilterHolder = document.querySelector('.js-mob-filter-holder');
        const mobFilter = document.querySelector('.js-mob-filter');
        const shadowFilter = document.querySelector('.js-shadow-filter');
        if(shadowFilter && mobFilter) {
            if(min541.matches){
                mobFilter.style.width = `${shadowFilter.clientWidth}px`;
            }
            // if(max540.matches){
            //     mobFilterHolder.append(mobFilter);
            // }
            window.addEventListener('resize', function (){
                if(min541.matches) {
                    mobFilter.style.width = `${shadowFilter.clientWidth}px`;
                }
                // if(max540.matches){
                //     $(mobFilter).appendTo( $(mobFilterHolder) );
                // }else{
                //     $(mobFilter).prependTo( $(filters.querySelector('.filter._first')) );
                // }
            });
        }

    }


}
