const moduleName = 'cmplx';

export function cmplx() {
    // const resetInput = (input) => {
    //     input.value = '';
    //     const event = document.createEvent('HTMLEvents');
    //     event.initEvent('change', true, true);
    //     input.dispatchEvent(event);
    // }
    // const resetSelect = (select) => {
    //     select.selectedIndex = 0;
    //     const event = document.createEvent('HTMLEvents');
    //     event.initEvent('change', true, true);
    //     select.dispatchEvent(event);
    // }
    const isVisible = (elem) => elem.offsetWidth > 0 || elem.offsetHeight > 0;
    const closeCMPLX = (cmplx) => {
        const input = cmplx.querySelector('.js-cmplx-input');
        cmplx.classList.remove('_opened');

        const event = new Event('change');
        input.dispatchEvent(event);
    }
    const openCMPLX = (cmplx) => {
        cmplx.classList.add('_opened');

        const filterInput = cmplx.querySelector('.js-cmplx-filter-input');
        if(filterInput){
            filterInput.focus();
        }
    }

    document.querySelectorAll('.js-cmplx').forEach((cmplx) => {
        const input = cmplx.querySelector('.js-cmplx-input');
        const dropdown = cmplx.querySelector('.js-cmplx-dropdown');
        input.addEventListener('click', function (e){
            if(!isVisible(dropdown)){
                openCMPLX(cmplx);
            }else{
                closeCMPLX(cmplx);
            }

        });
    });

    document.querySelectorAll('.js-cmplx-modal').forEach((cmplx) => {
        const input = cmplx.querySelector('.js-cmplx-input');
        const modal = cmplx.querySelector('.modal');
        input.addEventListener('click', function (e){
            toggleModal(modal.id);
        });
    });

    const getCMPLXValues = (cmplx) => {
        if(cmplx) {
            const dataSources = cmplx.querySelectorAll('.js-cmplx-data');
            let values = [];
            dataSources.forEach((el) => {
                if(el.type === 'checkbox'){
                   if(el.checked){
                       values.push(el.value);
                   }
                }else if(el.value) {
                    values.push(el.value);
                }else{
                    if (el.classList.contains('geo-select') && el instanceof HTMLSelectElement) {
                        values.push(-1);
                    }
                }
            });
            return values.join(', ');
        }
        return false;
    }

    // const getCMPLXOptionsText = (cmplx) => {
    //     if(cmplx) {
    //         const dataSources = cmplx.querySelectorAll('.js-cmplx-data');
    //         let text = [];
    //         dataSources.forEach((el) => {
    //             const $selectize = $(el)[0].selectize;
    //             if($selectize) {
    //                 if($selectize.$activeOption){
    //                     text.push($selectize.$activeOption[0].innerText);
    //                 }
    //             }
    //             // const index = el.selectedIndex;
    //             // if(index) text.push(el.options[index].text);
    //         });
    //
    //         return text.join(', ');
    //     }
    //
    //     return false;
    // }

    const getCMPLXOptionsText = (cmplx) => {
        if(cmplx) {
            const dataSources = cmplx.querySelectorAll('.js-cmplx-data');
            let text = [];
            dataSources.forEach((el) => {
                const $selectize = $(el)[0].selectize;
                if($selectize) {
                    if($selectize.$activeOption){
                        text.push($selectize.$activeOption[0].innerText);
                    } else {
                        if (parseInt($(el).val()) > 0) {
                            text.push($(el).find('option:selected').first().text());
                        } else if ($(el)[0].hasAttribute("data-any-name")) {
                            text.push($(el).data('any-name'));
                        }
                    }
                }
                // const index = el.selectedIndex;
                // if(index) text.push(el.options[index].text);
            });

            return text.join(', ');
        }

        return false;
    }

    const getCMPLXCheckboxesText = (cmplx) => {
        if(cmplx) {
            const dataSources = cmplx.querySelectorAll('.js-cmplx-data');
            let text = [];
            dataSources.forEach((el) => {
                if(el.checked) text.push(el.nextElementSibling.querySelector('span:last-child').textContent);
            });

            return text.join(', ');
        }
        return false;
    }

    document.querySelectorAll('.js-cmplx-filter-input').forEach((input) => {
        input.addEventListener('keyup', function (){
            const cmplx = this.closest('.js-cmplx');
            const input = cmplx.querySelector('.js-cmplx-filter-input');
            const filter = input.value.toUpperCase();
            const dropdown = cmplx.querySelector('.js-cmplx-dropdown');
            const options = Array.from(dropdown.querySelectorAll('.check-wrap'));
            for (let i = 0; i < options.length; i++) {
                const span = options[i].querySelector('label > span:last-child');
                const txtValue = span.textContent || span.innerText;
                if (txtValue.toUpperCase().indexOf(filter) > -1) {
                    options[i].style.display = "";
                } else {
                    options[i].style.display = "none";
                }
            }
        });
    });

    document.querySelectorAll('.js-submit-modal-cmplx').forEach((btn) => {
        btn.addEventListener('click', function (){
            const cmplx = btn.closest('.js-cmplx-modal');
            const input = cmplx.querySelector('.js-cmplx-input');
            const value = getCMPLXValues(cmplx);
            if(value){
                input.value = value;
                input.title = value;
            }else{
                input.value = input.dataset.defaultValue;
                input.title = '';
            }

            const event = new Event('change');
            input.dispatchEvent(event);

            toggleModal(btn.closest('.modal').id);

        });
    });

    // document.querySelectorAll('.js-cmplx-reset').forEach((btn) => {
    //     const inputs = btn.closest('.js-cmplx').querySelectorAll('.js-cmplx-dropdown input');
    //     const selects = btn.closest('.js-cmplx').querySelectorAll('.js-cmplx-dropdown select');
    //     if(inputs) {
    //         inputs.forEach((input) => {
    //             resetInput(input);
    //         });
    //     }
    //     if(selects) {
    //         selects.forEach((select) => {
    //            resetSelect(select);
    //         });
    //     }
    //
    // });

    document.body.addEventListener('click', function (e){
        const target = e.target;
        const cmplx = document.querySelector('.js-cmplx._opened');
        if((target === cmplx) || target.classList.contains('js-cmplx-submit')) {
            e.stopPropagation();
            const input = cmplx.querySelector('.js-cmplx-input');
            const value = getCMPLXValues(cmplx);
            const text = input.classList.contains('js-cmplx-select-input') ? getCMPLXOptionsText(cmplx) : getCMPLXCheckboxesText(cmplx);
            if(value){
                if(text) {
                    input.value = text;
                    input.dataset.ids = value;
                    input.dataset.value = value;
                }else {
                    input.value = value;
                    input.title = value;
                }
            }else{
                input.value = input.dataset.defaultValue;
                input.dataset.ids = 0;
                input.title = '';
            }
            closeCMPLX(cmplx);
        }
    });

    //Получаем и устанавливаем количество отфильтрованных опций
    const filterInputs = document.querySelectorAll('.filter .js-cmplx-input');
    const filterSelects = document.querySelectorAll('.filter .cmplx-select');
    const getFilteredCount = (inputs) => {
        let filteredCount = 0;
        inputs.forEach((input) => {
            if(input.value !== input.dataset.defaultValue) {
                filteredCount++;
            }
        });
        filterSelects.forEach((select) => {
            if(select.value && select.value !== '0'){
                filteredCount++;
            }
            // const $selectize = $(select)[0].selectize;
            // if($selectize) {
            //     if($selectize.getValue()){
            //         filteredCount++;
            //     }
            // }
        });
        // if(document.querySelector('.js-price-select').value && document.querySelector('.js-price-select').value !== '0') {
        //
        // }
        // if(document.querySelector('.js-price-select').value && document.querySelector('.js-price-select').value !== '0') {
        //     filteredCount++;
        // }

        return filteredCount;
    }

    const setFilteredCount = (count, el) => {
        el.textContent = count;
        if(count) {
            el.classList.add('_visible');
        }else{
            el.classList.remove('_visible');
        }
    }
    if(filterInputs) {
        const filteredCountEl = document.querySelector('.filtered-count');
        filterInputs.forEach((input) => {
            input.addEventListener('change', function (e){
                setFilteredCount(getFilteredCount(filterInputs), filteredCountEl);
            });
        });
        // document.querySelector('.js-price-select').addEventListener('change', function (){
        //     setFilteredCount(getFilteredCount(filterInputs), filteredCountEl);
        // });
    }

    if(filterSelects) {
        const filteredCountEl = document.querySelector('.filtered-count');
        filterSelects.forEach((select) => {
            select.addEventListener('change', function (){
                setFilteredCount(getFilteredCount(filterInputs), filteredCountEl);
            });
            const $selectize = $(select)[0].selectize;
            if($selectize) {
                $selectize.on('change',function (){
                    setFilteredCount(getFilteredCount(filterInputs), filteredCountEl);
                });
            }
        });
    }
}
