const moduleName = 'tooltip';

export function tooltip() {
    const min1201 = window.matchMedia('(min-width: 1201px)');
    if(min1201.matches) {

    }
    document.querySelectorAll('.js-has-tooltip').forEach((el) => {
        el.addEventListener('mouseenter', function (){
            if(!el.classList.contains('block-tooltip')){
                const tooltip = document.createElement('span');
                tooltip.classList.add('tooltip','js-tooltip');
                if(el.classList.contains('tooltip-label') || el.classList.contains('tooltip-element')) {
                    tooltip.classList.add('tooltip--light');
                }
                if(el.classList.contains('tooltip-element')) {
                    tooltip.classList.add('tooltip--inline');
                }
                tooltip.textContent = el.dataset.tooltip;
                el.appendChild(tooltip);
                tooltip.classList.add('_visible');
                if(tooltip.getBoundingClientRect().x + tooltip.offsetWidth > document.body.offsetWidth) {
                    tooltip.style.left = 'auto';
                    tooltip.style.right = '0';
                }
                if(!el.classList.contains('tooltip-label') && !el.classList.contains('tooltip-element')){
                    setTimeout(function () {
                        tooltip.classList.add('_hidden-up');
                        tooltip.classList.remove('_visible');
                        tooltip.remove();
                    }, 650);
                }

            }

        });
        el.addEventListener('mouseleave', function (){
            const tooltip = this.querySelector('.js-tooltip');
            if(tooltip){
                tooltip.classList.add('_hidden');
                // setTimeout(function () {
                //     tooltip.classList.remove('_visible');
                //     tooltip.remove();
                // }, 300);
                tooltip.classList.remove('_visible');
                tooltip.remove();
            }
        });
    });
}


